import 'svelte/internal/disclose-version';
import * as $ from 'svelte/internal/client';
import Icon from './Icon.svelte'; // Assuming Icon.svelte is in the same directory

var root = $.template(`<button class="read-status" aria-label="Chapter read status"><!></button>`);

function ChapterRead($$anchor, $$props) {
	var button = root();
	var node = $.child(button);

	Icon(node, { icon: 'lucide:eye-off' });
	$.reset(button);
	$.template_effect(() => $.set_attribute(button, 'data-id', $$props.chapterId));
	$.append($$anchor, button);
}

if (import.meta.hot) {
	ChapterRead = $.hmr(ChapterRead, () => ChapterRead[$.HMR].source);

	import.meta.hot.accept((module) => {
		$.cleanup_styles('svelte-uwe6fx9cxe6z');
		module.default[$.HMR].source = ChapterRead[$.HMR].source;
		$.set(ChapterRead[$.HMR].source, module.default[$.HMR].original);
	});
}

export default ChapterRead;

if (customElements.get('my-element') == null) customElements.define('my-element', $.create_custom_element(ChapterRead, { route: {}, chapterId: {} }, [], [], true));
import "bun-svelte:ChapterRead.svelte-2bhwrp86m3dqk-style.css";